import React from "react";
import { Link } from "react-router-dom";
import { Panel, PanelHeader } from "../../../components/panel/panel.jsx";

import {
  ShowModalSales,
  showModal,
  getDataSales,
  hideModal,
} from "../../../actions/datamaster_action.jsx";
import { connect } from "react-redux";
import Swal from "sweetalert2";
import {
  NotifSucces,
  ToastNotification,
} from "../../../components/helpers/notification.jsx";
import { reset } from "redux-form";
import { deleteData, postData, putData } from "../../../config/axios.jsx";
import { ErrorHandling } from "../../../components/helpers/function.jsx";
import Tabel from "../../../components/helpers/tabel.jsx";
import FormModalSales from "./FormModalSales.jsx";
import ModalGlobal from "../../../components/helpers/ModalGlobal.jsx";
import { PageSettings } from "../../../config/page-settings";

const maptostate = (state) => {
  return {
    DataSales: state.datamaster.getDataSales,
    hideModal: state.datamaster.modalDialog,
  };
};
const hapusDataSales = (kode, params, dispatch) => {
  Swal.fire({
    // title: "Anda Yakin !!",
    // text: "Ingin Menghapus Data Ini ?",
    html:
      "Apakah Anda Yakin Ingin " +
      "Menghapus " +
      "<h1><b>Kode Sales " +
      kode +
      "</b> ?</h1>",
    icon: "warning",
    position: "top-center",
    cancelButtonText: "Tidak",
    showCancelButton: true,
    confirmButtonText: "OK",
    showConfirmButton: true,
  }).then((result) => {
    if (result.isConfirmed) {
      deleteData("sales/delete/by-kode-sales/" + params)
        .then(() => {
          NotifSucces("Data Berhasil Di Hapus").then(() =>
            dispatch(getDataSales())
          );
        })
        .catch((err) => {
          ToastNotification("error", err.response.data);
        });
    }
  });
};
class MasterSales extends React.Component {
  static contextType = PageSettings;
  constructor(props) {
    super(props);
    this.state = {
      isEdit: false,
      modalDialog: false,
      isLoading: false,
      columns: [
        {
          dataField: "kode_sales",
          text: "Kode Sales",
          sort: true,
        },
        {
          dataField: "nama_sales",
          text: "Nama Sales",
        },
        {
          dataField: "action",
          text: "Action",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row) => {
            let dataEdit = {
              id: row._id,
              kode_sales: row.kode_sales,
              nama_sales: row.nama_sales,
            };

            return (
              <div className="row text-center">
                <div className="col-12">
                  <button
                    onClick={() => this.formEditSales(dataEdit)}
                    className="btn btn-primary mr-3"
                  >
                    <i className="fa fa-edit"></i>
                  </button>
                  <button
                    onClick={() =>
                      hapusDataSales(
                        row.kode_sales,
                        row._id,
                        this.props.dispatch
                      )
                    }
                    className="btn btn-danger"
                  >
                    <i className="fa fa-trash"></i>
                  </button>
                </div>
              </div>
            );
          },
        },
      ],
      // dataSales: [
      //   {
      //     kode_sales: "SAM",
      //     nama_sales: "Samsul Arifin",
      //   },
      // ],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  componentDidMount() {
    this.props.dispatch(getDataSales());
    this.context.handleSetPageSidebar(false);
    this.context.handleSetPageHeader(false);
  }
  handleSubmit(data) {
    this.setState({
      isLoading: true,
    });
    let data_edit = {
      nama_sales: data.nama_sales,
    };
    let data_post = {
      kode_sales: data.kode_sales,
      nama_sales: data.nama_sales,
    };
    this.state.isEdit
      ? putData("sales/edit/by-kode-sales/" + data.id, data_edit)
          .then(() => {
            this.props.dispatch(hideModal());
          })
          .then(() => {
            ToastNotification("success", "Data Berhasil Diedit");
          })
          .then(() => {
            this.props.dispatch(getDataSales());
          })
          .then(() => {
            this.setState({ isLoading: false });
          })
          .then(() => {
            this.props.dispatch(reset("ModalDataSales"));
          })
          .catch((error) => {
            ToastNotification(
              "info",
              error.response === undefined
                ? "Terjadi Kesalahan Saat Megirim Data "
                : error.response.data
            );
            this.setState({ isLoading: false });
          })
      : postData("sales/add", data_post)
          .then(() => {
            this.props.dispatch(hideModal());
          })
          .then(() => {
            ToastNotification("success", "Data Berhasil Simpan");
          })
          .then(() => {
            this.props.dispatch(getDataSales());
          })
          .then(() => {
            this.props.dispatch(reset("ModalDataSales"));
          })
          .then(() => {
            this.setState({ isLoading: false });
          })
          .catch((error) =>
            ErrorHandling(
              error,
              "sales/reactive/by-kode-sales/",
              // data.kode_sales,
              data_post,
              this.props.dispatch,
              getDataSales(),
              hideModal(),
              "ModalDataSales"
            ).then(() => {
              this.setState({ isLoading: false });
            })
          );
  }
  formEditSales(dataEdit) {
    this.props.dispatch(showModal());
    this.props.dispatch(ShowModalSales(dataEdit));
    this.setState({
      isEdit: true,
    });
  }
  showModalSales() {
    this.props.dispatch(showModal());
    this.props.dispatch(ShowModalSales(false));
    this.setState({
      isEdit: false,
    });
  }
  render() {
    return (
      <div>
        <Link to="/SubMenuUtility">
          <button
            type="button"
            className="btn btn-secondary"
            data-tut="reactour_4"
          >
            <i className="fa fa-angle-left mr-3" aria-hidden="true"></i> Home
          </button>
        </Link>
        <br />
        <br />
        <Panel>
          <PanelHeader>Master Sales</PanelHeader>
          <br />

          {/* Master Sales */}
          <div className="container">
            <Tabel
              keyField="kode_sales"
              tambahData={true}
              handleClick={() => this.showModalSales()}
              exportCsv={true}
              data={this.props.DataSales}
              columns={this.state.columns}
              empty={this.props.DataSales}
              textEmpty="Data Sales Kosong"
            />
          </div>
          <br />
          {/* End Master Sales */}
          <ModalGlobal
            title={this.state.isEdit ? "Edit Data Sales" : "Tambah Data Sales"}
            size="P"
            content={
              <FormModalSales
                isLoading={this.state.isLoading}
                isEdit={this.state.isEdit}
                onSubmit={(data) => this.handleSubmit(data)}
              />
            }
          />

          {/* <Modal  title={ this.state.isEdit ? 'Edit Data Sales' : 'Tambah Data Sales' } content={ <FormModalKategori isEdit={this.state.isEdit} /> } isOpen={ this.props.hideModal} onSubmit={ (data)=>this.handleSubmit(data) } /> */}

          {/* End Tambah Master Sales  */}
        </Panel>
      </div>
    );
  }
}

export default connect(maptostate, null)(MasterSales);
