import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { hideModal } from "../../../actions/datamaster_action";
import { HiidenFiled, ReanderField } from "../../../components/helpers/field";
import ValidasiMasterSales from "../../../Validasi/ValidasiMasterSales.jsx";
// import Loading from 'react-fullscreen-loading';

const maptostate = (state) => {
  if (state.datamaster.ShowModalSales !== null) {
    return {
      initialValues: {
        id: state.datamaster.ShowModalSales.id,
        kode_sales: state.datamaster.ShowModalSales.kode_sales,
        nama_sales: state.datamaster.ShowModalSales.nama_sales,
      },
    };
  }
};
class FormModalSales extends Component {
  state = {
    aktif : false
  };
  componentDidMount() {
    setTimeout(() => {
      this.props.isEdit
      ? document.getElementById("nama_sales").focus()
      : document.getElementById("kode_sales").focus()
    }, 100);
  }
  render() {
    return (
      <form
        onSubmit={this.props.handleSubmit}
        onKeyPress={(e) => {
          e.key === "Enter" && e.preventDefault();
        }}
        autoComplete="off"
      >
        {
          this.props.isEdit ? <>
             <Field
              name="id"
              component={HiidenFiled}
              type="hidden"
              readOnly={this.props.isEdit}
            />
          </> : null
        }
        <Field
          tabIndex="1"
          id="kode_sales"
          name="kode_sales"
          component={ReanderField}
          type="text"
          label="Kode Sales"
          placeholder="Masukan Kode Sales"
          readOnly={this.props.isEdit}
        />
        <Field
          id="nama_sales"
          tabIndex="2"
          name="nama_sales"
          component={ReanderField}
          type="text"
          label="Nama Sales"
          onBlur={() => this.setState({ aktif: true })}
          placeholder="Masukan Nama Sales"
        />
        <div className="row">
          <div className="col-12 text-right">
            <button
              className="btn btn-white"
              onClick={() => this.props.dispatch(hideModal())}
              type="button"
              disabled={this.props.isLoading}
            >
              Batal
            </button>
            &nbsp;
            <button
              tabIndex="3"
              className={this.state.aktif ? "btn btn-primary" : "btn btn-white"}
              disabled={this.props.submitting}
              type="submit"
            >
              {this.props.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Menyimpan
                </>
              ) : (
                "Simpan Data"
              )}
            </button>
          </div>
        </div>
         {/* {/* {this.state.isLoading ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null} */}
      </form>
    );
  }
}

FormModalSales = reduxForm({
  form: "ModalDataSales",
  enableReinitialize: true,
  validate: ValidasiMasterSales,
})(FormModalSales);
export default connect(maptostate, null)(FormModalSales);
